

:root {
  --primary: #28a745; 
 
  --light: #F2F2F2;
  --dark-primary: #ecebf1;
  --grey: #A6A6A6;
  --dark: #262626;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: var(--primary) !important; */
  /* background: linear-gradient(180deg,#1cc88a,#13855c); */
  
  background: radial-gradient(circle,#4EAD16 35% , #085924);
  
  background-size: cover;
  height: 100vmin;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.user-img{
  margin-top: 10px;
  margin-bottom: 10px;
}
.user-img img{
  width: 150px;
  height: 150px;
  box-shadow: 0px 0px 3px var(--primary);
  border-radius: 50%;
  
}



body {
  /* overflow: hidden; */
  font-family: 'Muli', sans-serif;
}

.sidebar.close {
  left: 0;
  animation: slide-closed 2.0s forwards;
}

.sidebar-close{
  background: none;
  border: none;
  font-size: 2rem;
}

@keyframes slide-open {
100%{left: 0}
}
@keyframes slide-closed {
  100%{left: -100%}
}

h1, h2, h3, h4, h5, h6 {
  color: var(--primary) !important;
  font-weight: 700;
}

.sidebar-background{
  background: linear-gradient(225deg,#085924 10% , #4EAD16);
  width: 100px;
}
#sidebar-container{
  /* background-color: var(--dark); */
  
  width: 101px;
  
}

.navbar{
  background-color: var(--light);
  height: 50px;
  
}
.list-group-item{
  background: none !important;
  width: 100px;
  text-align: center;
  padding: 0;
  margin: 0;
}
.icon{
  font-size: 32px !important;
}

.card-info{
  background-color: white;
  border-left: 5px solid var(--primary) !important;
}
hr{
  margin: 0;
  padding: 0;

}
.circle{
  background-color: var(--primary);
}
.color-green{
  background-color: var(--primary);
}

.logo-sidebar{
  width: 50px;
  height: 50px;
  box-shadow: 0px 0px 6px var(--light);
  border-radius: 50%;
}
.logo{
  
}

.prueba{
 
}

.list-group-item:hover{
  background: white !important;
  color: black  !important;
}

.card-header{
  background-color: var(--primary);
}

.bg-light-blue {
  background-color: var(--light) !important;
}

.shadow-sm {
  box-shadow: 0 0 1.25rem var(--dark);
}

.shadow-hover {
  transition: 1s ease-in-out;
}

.shadow-hover:hover {
  transition: 1s ease-in-out;
  box-shadow: 0 .5rem 1rem var(--dark)!important;
}



#sidebar-container .logo{
  padding: 0.875rem 1.25rem;
}



#content-wrapper.toggled #sidebar-container {
  margin-left: 0;
}

.circle {
  width: 50px;
  height: 50px;
}

.badge {
  padding: .5rem 1rem;
}


.avatar {
  max-width: 35px;
}


.badge-secondary {
  background-color: var(--grey);
  color: var(--primary);
}

#content {
  overflow-y: auto;
  height: 100vh;
}
table {
  border-collapse: separate;
  border-spacing: 0 .5rem;
}

table thead th {
  border: 0 !important;
}

table tbody tr {
  background-color: var(--light);
}

table tbody td {
  border-top: 0 !important;
  line-height: 1.2;
}


@media (min-width: 768px) {
  #sidebar-container {
    margin-left: 0;
  }
  #content-wrapper.toggled #sidebar-container {
    margin-left: -18rem;
  }
}

.bar-one{
  width: 20%;
}
.bar-two{
  width: 40%;
}
.bar-three{
  width: 60%;
}
.bar-four{
  width: 80%;
}
.bar-five{
  width: 100%;
}

.card{
  border: none;
  
}

.forgot{
  padding-top: 10px;
}

.pagination > li > a{
  background-color: var(--light);
  color: var(--primary)
}

.pagination > li > a:focus,
.pagination > li > a:hover,
.pagination > li > span:focus,
.pagination > li > span:hover{
  background-color: var(--primary);
  color: var(--light);
  border: none;
}

.pagination > li.active > a{
  color: var(--light);
  background-color: var(--primary) !important;
  border: none;
}

.button-action{
  width: 30px;
  height: 35px;
  margin-right: 3px;
  
}

.action{
  
  font-size: 20px !important;
  text-align: center !important;
 
}